<template>
  <div class="indexClassify">
    <div class="mainContent" @click="routerToPlayListPage(novelItem._id)">
      <div class="bookcover">
        <img :src="geturlcode()" />
      </div>
      <div class="bookText">
        <div class="booktitle">{{novelItem.title}}</div>
        <div class="bookDescript">{{novelItem.shortIntro}}</div>
        <div class="bookAuthor">
          <span>{{novelItem.author}}</span>
          <span
            class="fr"
            style="background-color: rgb(242, 130, 106)"
          >{{gethuman(novelItem.latelyFollower)}}</span>
          <span class="fr" style="background-color: #969799">{{novelItem.majorCate}}</span>
        </div>
      </div>
    </div>

    <div class="bookList">
      <bookList v-for="(item, index) in booklist" :key="index" :novelItem="item"></bookList>
    </div>
  </div>
</template>

<script>
import bookList from "@/components/bookList.vue";
export default {
  props: {
    novelItem: {
      type: Object
    },
    booklist:{
        type:Array
    }
  },
  components: {bookList},
  data() {
    return {};
  },
  methods: {
    geturlcode() {
      let coverCode = decodeURIComponent(this.novelItem.cover);
      coverCode = coverCode.substring(7);
      return coverCode;
    },
    routerToPlayListPage: function(id) {
      this.$router.push({ path: "bookDetails", query: { id: id } });
    },
    gethuman(humanCount) {
      return (humanCount / 10000).toFixed(1) + "万人气";
    }
  },
  created() {
    if (this.novelItem) {
      this.showIf = true;
    }
  }
};
</script>

<style lang="less" scoped>
.mainContent {
  display: flex;
  .bookcover {
    flex: 2;
    img {
      max-width: 100%;
      height: auto;
      display: block;
    }
  }
  .bookText {
    flex: 7;
    padding-left: 10px;

    .booktitle {
      margin: 5px 0;
      font-size: 16px;
    }
    .bookDescript {
      height: 3em;
      font-size: 14px;
      line-height: 1.5em;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      margin: 5px 0;
    }
    .bookAuthor {
      font-size: 14px;
      line-height: 20px;
      padding: 5px 0;
      .fr {
        display: block;
        float: right;
        border-radius: 999px;
        padding: 0 8px;
      }
    }
  }
}
.bookList {
    display: flex;
  }
</style>