<template>
  <div class="classifyPage">
      <HomeLink></HomeLink>
      <div class="nav">
      <div class="returnPre" @click="$router.go(-1)">
        <!-- <i class="fa fa-chevron-left" aria-hidden="true"></i> -->
        <span style="font-size:18px">〈 </span>
         <span> 返回</span>
      </div>
      <div class="title">分类</div>
      <div class="jump"></div>
    </div>
    <div class="topImg">
      <div>
        <img src="../assets/lunbo1.jpg" />
      </div>
      <div>
        <img src="../assets/lunbo3.jpg" />
      </div>
    </div>

    <template v-if="showIf">
      <van-tabs id="vanTitle" v-model="active">
        <!-- 用到v-for循环对象 -->
        <van-tab :title="TagBar[index]" v-for="(value, key, index) in clify" :key="index">
          <classifyCardBox 
            :bookList="value"
            :subcategory="subcategory[key]"
            :classTag="key"
            @s-subcategory="$emit('s-subcategory',$event)"
            @s-classTag="$emit('s-classTag',$event)"
          ></classifyCardBox>
        </van-tab>
      </van-tabs>
    </template>
  </div>
</template>

<script>
import Vue from "vue";
import { Tab, Tabs } from "vant";
import classifyCardBox from "@/components/classifyCardBox.vue";
import HomeLink from "@/components/HomeLink.vue";

Vue.use(Tab);
Vue.use(Tabs);

export default {
  components: {
    classifyCardBox,
    HomeLink
  },
  data() {
    return {
      active: 0,
      clify: null,
      showIf: false,
      subcategory: null,
      TagBar:["男生","女生","标签","出版"]
    };
  },
  methods: {},
  created() {
    // 获取带书籍数量的父分类
    // 用于书单内的书本列表渲染
    this.axios
      .get("/categories")
      .then(response => {
        console.log(response);
        this.clify = response.data
        // 删除data数据对象里面的ok属性，目的为了不影响四个标签板块的循环操作
        Vue.delete(this.clify,"ok")
      })
      .catch(function(error) {
        console.log(error);
      });

    // 获取带子分类的分类
    // 用于点击书单后的Tab标签栏动态显示
    this.axios
    .get("/sub-categories")
    .then(response => {
      console.log(response);
      this.subcategory = response.data;
      // 等数据都获取到了再渲染页面，避免页面渲染先于数据而报错
      this.showIf = true;
    })
    .catch(function(error) {
        console.log(error);
      });
  }
};
</script>

<style lang="less" scoped>
.classifyPage{
  padding-bottom: 50px;
}
.nav {
  display: flex;
  height: 50px;
  align-items: center;
  padding: 0 10px;
  background: #b83320;
  color: white;
  margin-bottom: 1px;
  .returnPre{
    font-size: 14px;
  }
  & > div {
    flex: 1;
  }
  .title {
    flex: 2;
    text-align: center;
  }
}
.topImg {
  display: flex;
  padding: 10px;
  & > div {
    flex: 1;
    // flex-grow: 50%;
    margin: 0 5px;
    img {
      max-width: 100%;
    }
  }
}
/deep/ #vanTitle .van-tab {
  font-size: 18px;
  font-weight: 600;
}
</style>