<template>
  <div class="bookdetail">
    <div class="loading" v-if="showLoading">
      <van-loading type="spinner" size="36px"/>
    </div>
    <div class="nav">
      <div class="returnPre" @click="$router.go(-1)">
        <!-- <i class="fa fa-chevron-left" aria-hidden="true"></i> -->
        <span style="font-size:18px">〈 </span>
         <span> 返回</span>
      </div>
      <div class="title">书籍详情页</div>
      <div class="jump"></div>
    </div>
    <template v-if="detail">
      <div class="Topflex">
        <div class="bookImg">
          <img :src="geturlcode()" />
        </div>
        <div class="bookText">
          <div class="title">{{bookDetail.title}}</div>
          <div class="text">
            <span>{{bookDetail.author}}</span> |
            <span>{{bookDetail.copyright}}</span>
          </div>
          <div class="text">
            <span>{{bookDetail.cat}}</span> |
            <span>{{getword(bookDetail.wordCount)}}</span>
          </div>
        </div>
      </div>

      <div class="appraise">
        <div class="gradestart">
          <div>{{bookDetail.rating.score}}</div>
          <div>{{bookDetail.rating.tip}}</div>
        </div>
        <div class="readers">
          <div>{{bookDetail.retentionRatio+'%'}}</div>
          <div>读者留存</div>
        </div>
        <div class="popularity">
          <div>{{gethuman(bookDetail.latelyFollower)}}</div>
          <div>7天人气</div>
        </div>
        <div class="total">
          <div>{{gethuman(bookDetail.totalFollower)}}</div>
          <div>累计人气</div>
        </div>
      </div>

      <div class="briefIntroduction">
        <div class="title">简介</div>
        <div class="content">
          <div class="tags">
            <i v-for="(tag, index) in bookDetail.tags" :key="index">{{tag}}</i>
          </div>
          <div @click="showMoreDesc = !showMoreDesc">
            <div class="desc" :class="{show: showMoreDesc}">
              <span>简介：</span>
              <span>{{bookDetail.longIntro}}</span>
            </div>
            <div class="more">
              <i v-if="showMoreDesc" class="fa fa-angle-up"></i>
              <i v-else class="fa fa-angle-down"></i>
            </div>
          </div>

          <!-- <div class="longItroduce">{{bookDetail.longIntro}}</div> -->
        </div>
      </div>

      <div class="catalog">
        <div>目录</div>
        <div class="updated">
          <span style="color:#e4393c">{{bookDetail.updated}}</span>
          <span style="color:#9e9da2">{{bookDetail.lastChapter}}</span>
        </div>
        <div style="text-align:right">></div>
      </div>
    </template>

    <!-- 底部阅读栏 -->
    <div class="position">
      <div class="readBox">
        <div class="CatchUp" v-if="catchUp" @click="catchUp=!catchUp">+追更</div>
        <div class="hide" v-else @click="catchUp=!catchUp">不追了</div>
        <div class="starRead" @click="toRead(bookId)">开始阅读</div>
      </div>
    </div>
  </div>
</template>
jia
<script>
import Vue from 'vue';
import { Loading } from 'vant';

Vue.use(Loading);

export default {
  data() {
    return {
      bookDetail: null,
      detail: false,
      showMoreDesc: false,
      catchUp:true,
      bookId:null,
      showLoading:false
    };
  },
  methods: {
    getword(wordCount) {
      return (wordCount / 10000).toFixed(1) + "万字";
    },
    gethuman(humanCount) {
      return (humanCount / 10000).toFixed(1) + "万";
    },
    geturlcode() {
      let coverCode = decodeURIComponent(this.bookDetail.cover);
      coverCode = coverCode.substring(7);
      return coverCode;
    },
    toRead(bookId){
      console.log(bookId)
      this.$router.push({ path: "readPage", query: { bookId: bookId,bookName:this.bookDetail.title} });
    }
  },

  beforeRouteEnter(to, from, next) {
   
    // console.log(to.query.id);
    window.axios
      .get("/book-info/" + to.query.id)
      .then(response => {
        console.log(to.query.id);
        next(vm => {
          // 有意思的是，这里的response输出居然受后面的代码影响了！！
          console.log(response);
          let updata = response.data.updated;
          // 获取正确的时间格式
          updata = updata.replace("T", " ");
          updata = updata.substr(0, updata.length - 1);
          updata = updata.replace(".", ":");
          console.log(updata);
          // 获取当前时间戳与更新时的时间戳
          let date = new Date(updata);
          let currentDate = new Date().getTime();
          // console.log("当前时间戳--" + currentDate);
          // console.log("updated时间戳--" + date.getTime());
          // 获取更新的时间间隔
          let updatedTime = currentDate - date;

          var year = parseInt(updatedTime / (1000 * 3600 * 24));
          var month = parseInt(updatedTime / (1000 * 3600)) % 24;
          if (year >= 1) {
            if (year <= 365) {
              response.data.updated = year + "天前更新";
            } else {
              response.data.updated = parseInt(year / 365) + "年前更新";
            }
          } else {
            response.data.updated = month + "小时前更新";
          }
          vm.bookId=to.query.id
          vm.bookDetail = response.data;
          vm.detail = true;
          console.log(response.data.title);

           vm.showLoading = false;
        });
      })
      .catch(function(error) {
        error;
        console.log(1);
      });
  },

  beforeRouteLeave(to, from, next) {
    // 导航离开该组件的对应路由时调用
    // 可以访问组件实例 `this`
    // console.log(to, from, "即将离开home");
    this.showLoading = true;
    next();
  }
};
</script>

<style lang="less" scoped>
