<template>
  <li class="flexbox" @click="routerToPlayListPage(novelItem._id)">
    <div class="cover">
      <img
        :src="geturlcode()"
      />
    </div>
    <div class="text">
      <div class="title">{{novelItem.title}}</div>
      <div class="descript">{{novelItem.shortIntro}}</div>
      <div class="author">{{novelItem.author}}</div>
    </div>
  </li>
</template>
<script>
export default {
  props: {
    novelItem: {
      type: Object
    },
  },
  data() {
    return {
      itemCover:null
    };
  },
  methods: {
    geturlcode(){
      let coverCode=decodeURIComponent(this.novelItem.cover)
      coverCode=coverCode.substring(7)
      return coverCode
    },
    routerToPlayListPage: function(id) {
      this.$router.push({ path: "bookDetails", query: { id: id } });
    }
  },
};
</script>
<style lang="less" scoped>
.flexbox {
  display: flex;
  padding: 10px;
 
  // height: 100%;
  .cover {
    flex: 2;
    img {
      max-width: 100%;
      height: auto;
      display: block;
    }
  }
  .text {
    flex: 7;
    padding-left: 10px;

    .title {
      margin: 5px 0;
      font-size: 16px;
    }
    .descript {
      height: 3em;
      font-size: 14px;
      line-height: 1.5em;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      margin: 5px 0;
    }
    .author{
      font-size: 14px;
    }
  }
}
</style>