import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import bookHome from "../views/bookHome.vue";
import Search from "../views/Search.vue";
import My from '../views/My.vue'
import bookDetails from "../components/bookDetails.vue";
import readPage from "../components/readPage.vue";
import classifyPage from "../components/classifyPage.vue";
import getClifyDetail from "../components/getClifyDetail.vue";

Vue.use(VueRouter)

const routes = [
  {
    path: "/",
    name: "bookHome",
    component: bookHome,
  },
  {
    path: "/Home",
    name: "Home",
    component: Home

  },
  {
    path: "/search",
    name: "Search",
    component: Search,
  },
  {
    path: "/My",
    name: "My",
    component: My,
  },
  {
    path: "/bookDetails",
    name: "bookDetails",
    component: bookDetails,
  },
  {
    path: "/readPage",
    name: "readPage",
    component: readPage,
  },
  {
    path: "/classifyPage",
    name: "classifyPage",
    component: classifyPage,
  },
  {
    path: "/getClifyDetail",
    name: "getClifyDetail",
    component: getClifyDetail,
  }
]

const router = new VueRouter({
  routes
})

export default router
