<template>
  <div class="classifyCard">
    <div class="title">
      <slot></slot>
    </div>
    <div class="content">
      <!-- bookList是父组件传下来的各类书单入口列表 -->
      <!-- 一个clifyCard组件是书单里面的一本书 -->
      <clifyCard
        v-for="(item,index) in bookList"
        :key="index"
        :cardItem="item"
        :cardItemIndex="index"
        :subcategoryItem="subcategory[index]"
        :classTag="classTag"
        @s-subcategory="$emit('s-subcategory',$event)"
        @s-classTag="$emit('s-classTag',$event)"
      ></clifyCard>
    </div>
  </div>
</template>

<script>
import clifyCard from "@/components/clifyCard.vue";
export default {
  props: {
    bookList: {
      type: Array
    },
    // 获取每本书的父分类和子分类（即书单里面tab栏的内容）
    subcategory: {
      type: Array
    },

    // 获取分类主页Tab上四个标签在数据里代表的属性名
     classTag: {
      type: String
    }
  },
  components: {
    clifyCard
  }
};
</script>

<style lang="less" scoped>
.content {
  display: flex;
  flex-wrap: wrap;
  // padding: 15px;
}
</style>