<template>
  <div id="app">
    <div id="nav">
      <router-view
        @s-subcategory="subcategory"
        @s-classTag="classTag=$event"
        :clifyMins="clifyMins"
        :classTag="classTag"
        @onfocus="dealfocus(arguments)"
        :onfocus="onfocus"
      ></router-view>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      clifyMins: null,
      classTag: null,
      onfocus:null
    };
  },
  methods: {
    subcategory($event) {
      console.log($event);
      // 获取子分类
      this.clifyMins = $event.mins;
    },
    // 子传父携带多个参数时用argument表示，然后将argument转为数组
    dealfocus(arr){
      // 之所以在此时而没有在初始化将onfocus变为数组，
      // 是为了防止直接进入搜索页面时this.onfocus没有携带数据造成报错，因为作为判断条件时空数组也是true
      this.onfocus=[]
      this.onfocus[0]=arr[0]
      this.onfocus[1]=arr[1]
      
    }
  }
};
</script>
