<template>
  <div class="clifyCard" @click="touch">
    <div class="Text">
      <div class="title">{{cardItem.name}}</div>
      <div class="bookSum">{{cardItem.bookCount+'本'}}</div>
    </div>
    <!-- 封面有三张图片，所以要渲染三个img -->
    <div class="coverBox">
      <img :src="geturlcode(0)" />
      <img :src="geturlcode(1)" />
      <img :src="geturlcode(2)" />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    cardItem: {
      type: Object
    },
    cardItemIndex: {
      type: Number
    },
    subcategoryItem:{
      type:Object
    },
     classTag: {
      type: String
    }
  },
  methods: {
    // api文档要求图片要进行编码
    geturlcode(index) {
      let coverCode = decodeURIComponent(this.cardItem.bookCover[index]);
      coverCode = coverCode.substring(7);
      // 预防有些图片加载失败，所以加上了前面的代理
      return "https://statics.zhuishushenqi.com/agent/" + coverCode;
    },
    // 进行路由跳转并传参
    routerToPlayListPage: function(name) {
      this.$router.push({path: "getClifyDetail",query:{id:name}});
    },
    // 向父组件传递数据
    touch(){
      // 点击后，执行路由跳转
      this.routerToPlayListPage(this.cardItem.name)
      // 点击后开始向父组件回传数据
      // 回传书籍主分类的数据
      this.$emit('s-subcategory',this.subcategoryItem)
      // 回传分类主页各版面的键名
      this.$emit('s-classTag',this.classTag)
    }
  },
  created() {
     
  },
};
</script>

<style lang="less" scoped>
.clifyCard {
  display: flex;
  flex-basis: 45%;
  margin: 10px 4px;
  padding: 15px 5px;
  background: #f1f1f1;
  .Text {
    flex: 5;
  }
  .title {
    font-size: 16px;
    color: #666;
  }
  .bookSum {
    font-size: 12px;
    color: #999;
  }
  .coverBox {
    flex: 5;
    position: relative;
    img {
      width: 45px;
      position: absolute;
    }
    img:first-child {
      transform: scale(0.8) translateY(3px);
      top: -10px;
      left: 0px;
    }
    img:nth-child(2) {
      transform: scale(0.8) translateY(3px);
      top: -10px;
      right: 0;
    }
    img:last-child {
      //   transform: translate(-50%);
      top: -10px;
      right: 18%;
      z-index: 1;
    }
  }
}
</style>