<template>
  <div class="bookItem" @click="routerToPlayListPage(novelItem._id)">
    <img :src="geturlcode()" />
    <span class="title">{{novelItem.title}}</span>
  </div>
</template>

<script>
export default {
  props: {
    novelItem: {
      type: Object
    }
  },
  methods: {
    geturlcode() {
      let coverCode = decodeURIComponent(this.novelItem.cover);
      coverCode = coverCode.substring(7);
      return coverCode;
    },
    routerToPlayListPage: function(id) {
      this.$router.push({ path: "bookDetails", query: { id: id } });
    },
  }
};
</script>

<style lang="less" scoped>
.bookItem {
  flex: 1;
  flex-basis: 25%;
  flex-grow: 1;
  align-items: center;
  padding: 10px;
  text-align: center;
  img {
    max-width: 100%;
    display: block;
  }
  .title {
    font-size: 12px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    margin-top: 8px;
  }
}
</style>