<template>
  <div>
    <form class="search-input">
      <div class="input-cover">
        <i class="searchIcon" @click="$emit('s-input', inputValue)"></i>
        <van-swipe
          style="height: 30px; line-height:30px"
          vertical
          :autoplay="loop"
          :show-indicators="false"
        >
          <van-swipe-item v-for="(item,index) in hotcommend" :key="index">
            <input
              type="text"
              v-model="inputValue"
              :placeholder="hotcommend[index]"
              @focus="handel(index)"
              @blur="loop=3000"
              :ref="'myInput'+index"
            />
          </van-swipe-item>
        </van-swipe>
        <i class="clearIcon" @click="inputValue=''"></i>
      </div>
    </form>
    <!-- <input ref="myInput"/> -->
  </div>
</template>
<script>
// @input="$emit('s-input', $event.target.value)"
import Vue from "vue";
import { Swipe, SwipeItem } from "vant";

Vue.use(Swipe);
Vue.use(SwipeItem);

export default {
  props: {
    hotcommend: {
      type: Array
    },
    onfocus:{
      type:Array
    }
  },
  data() {
    return {
      inputValue: null,
      loop: 3000
    };
  },
  watch: {
    inputValue: function(newValue) {
      this.$emit("s-input", newValue);
    }
  },
  methods: {
    handel(index) {
      this.$emit("s-focus",index);
      this.loop = 0;
    }
  },
  mounted() {
    console.log(this.onfocus)

    // console.log(myInput)
    if (this.onfocus) {
    var myInput='myInput'+this.onfocus[1]
      setTimeout(() => {
        console.log(this.$refs.myInput1[0]);
        this.$refs[myInput][0].focus();
      }, 100);
    }

    //  this.$refs.myInput[0].focus()
  }
};
</script>
<style lang="less" scoped>
.search-input {
  padding: 15px 10px;
  .input-cover {
    position: relative;
    width: 100%;
    height: 30px;
    padding: 0 30px;
    box-sizing: border-box;
    background: #ebecec;
    border-radius: 30px;
    input {
      width: 100%;
      height: 30px;
      line-height: 18px;
      background: rgba(0, 0, 0, 0);
      border: none;
      font-size: 14px;
      color: #333;
    }
    input:focus {
      outline: 0;
    }
    .searchIcon {
      width: 13px;
      height: 13px;
      background-image: url(data:image/svg+xml;base64,);
      position: absolute;
      left: 0;
      top: 9px;
      margin: 0 8px;
      vertical-align: middle;
    }
    .clearIcon {
      vertical-align: middle;
      width: 14px;
      height: 14px;
      position: absolute;
      right: 0;
      top: 9px;
      margin: 0 8px;
      background-image: url(data:image/svg+xml;base64,);
    }
  }
}
</style>