<template>
    <div>
        <HomeLink></HomeLink>
        <navTitle>搜索</navTitle>
        <searchInput @s-input="getSearch" @s-focus="showorhide=true" :onfocus="onfocus" :hotcommend="searchCommend"></searchInput>
        <hr>
        <template v-if="showorhide">
        <div class="hotWord">
          <div class="title">热门搜索</div>
          <div class="more">查看更多 ></div>
        </div>
        <div class="wordList">
          <searchHot v-for="(item,index) in hotWords" :key="index" :hotWord="item" @s-search='getSearch'></searchHot>
        </div>

        <div class="hotWord">
          <div class="title">热门推荐</div>
          <div class="more">换一批 <i class="fa fa-repeat" aria-hidden="true"></i></div>
        </div>
        <hr>
        <div class="flex">
          <hotCommend @s-search='getSearch' v-for="(item,index) in searchCommend" :key="index" :hotWord="item"></hotCommend>
        </div>
         </template>
        <template v-if="!showorhide">
        <ul>
          <searchContent v-for="(item,index) in books" :key="index" :novelItem="item"></searchContent>
        </ul>
        </template>
    </div>
</template>
<script>
// @ is an alias to /src
import HomeLink from '@/components/HomeLink.vue'
import navTitle from '@/components/navTitle.vue'
import searchInput from '@/components/searchInput.vue'
import searchContent from '@/components/searchContent.vue'
import searchHot from '@/components/searchHot.vue'
import hotCommend from '@/components/hotCommend.vue'

export default {
  name: 'Home',
  props:{
    onfocus:{
      type:Array
    }
  },
  components: {
    HomeLink,
    navTitle,
    searchInput,
    searchContent,
    searchHot,
    hotCommend
  },
  data() {
    return {
      books:null,
      hotWords:null,
      searchCommend:null,
      showorhide:true,
    }
  },
  methods: {
    getSearch: function(v) {
      this.showorhide = false;
      // console.log(v)
      if (!v) {
        this.showorhide=true
        this.books = [];
        return;
      }
      this.axios
        .get("/search", {
          params: {
            keyword: v
          }
        })
        .then(response => {
          var a=decodeURIComponent("http%3A%2F%2Fimg.1391.com%2Fapi%2Fv1%2Fbookcenter%2Fcover%2F1%2F233609%2F233609_136bccaa71d9468791744ce3a2a75b20.jpg%2F")
          console.log(a)
          this.books=response.data.books
          console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getHotWord(){
      this.axios
        .get("/search-hotwords")
        .then(response => {
          console.log(response);
          this.hotWords=response.data.searchHotWords.slice(0,8)
        })
        .catch(function(error) {
          console.log(error);
        });

        this.axios
        .get("/hot-books")
        .then(response => {
          console.log(response);
          this.searchCommend=response.data.hotWords
          console.log(this.searchCommend)
        })
        .catch(function(error) {
          console.log(error);
        });
    },
  },
  created() {
    this.getHotWord()
  },
}
</script>
<style lang="less" scoped>
  .hotWord{
    display: flex;
    padding: 15px 10px;
    .title{
      flex: 1;
      text-align: left;
    }
    .more{
      flex: 1;
      text-align: right;
      color: #a7a7a7;
      font-size: 14px;
    }
  }
  .wordList{
    padding: 0 15px;
  }
  .flex{
    display: flex;
    flex-wrap: wrap;
    padding: 0 15px;
  }
  hr{
    border: 0 ;
    border-bottom: 1px solid #ebedf0;
  }
</style>