<template>
  <div>
    <div class="titleBar">
      <div class="return" @click="$router.go(-1)">
        <i class="fa fa-chevron-left" aria-hidden="true"></i> 返回
      </div>
      <div class="title">{{clifytitle}}</div>
      <div></div>
    </div>
    <!-- 这里分为两个选择，男生版和女生版因为有子分类所以用van Tab组件 -->
    <template v-if="mins">
      <van-tabs id="vanTitle" v-model="active">
        <van-tab v-for="(item,index) in clifyMins" :key="index" :title="item">
          <searchContent v-for="(item,index) in novelItem[index]" :key="index" :novelItem="item"></searchContent>
        </van-tab>
      </van-tabs>
    </template>
    <!-- 标签和出版栏的书单里没有子分类，所以直接书本列表渲染 -->
    <searchContent v-else v-for="(item,index) in novelItem[0]" :key="index" :novelItem="item"></searchContent>
  </div>
</template>

<script>
import Vue from "vue";
import { Tab, Tabs } from "vant";

import searchContent from "@/components/searchContent.vue";

Vue.use(Tab);
Vue.use(Tabs);
export default {
  props: {
    clifyMins: {
      type: Array
    },
    classTag: {
      type: String
    }
  },
  components: {
    searchContent
  },
  data() {
    return {
      active: 0,
      clifytitle: null,
      novelItem: [],
      mins: true
    };
  },
  methods: {
    getnovelItem(index, name, classTag) {
      const cacheBookDetail = JSON.parse(
        window.localStorage.getItem("sl-" + name + this.clifyMins[index])
      );
      if (cacheBookDetail && cacheBookDetail.expire > Date.now()) {
        Vue.set(this.novelItem, index, cacheBookDetail.books);
      } else {
        window.axios
          .get("/category-info", {
            params: {
              gender: classTag,
              type: "hot",
              major: name,
              minor: this.clifyMins[index],
              start: 0,
              limit: 20
            }
          })
          .then(response => {
            // 获取到的数据放入本地存储
            window.localStorage.setItem(
              "sl-" + name + this.clifyMins[index],
              // 过期时间1小时
              JSON.stringify({
                expire: Date.now() + 3 * 60 * 60 * 1000,
                books: response.data.books
              })
            );

            console.log(response);
            // this.novelItem[index] = response.data.books;
            Vue.set(this.novelItem, index, response.data.books);
            console.log(this.novelItem);
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.log(vm.clifyMins);
      //判断是否存在子分类
      if (vm.clifyMins.length) {
        vm.mins = true;
      } else {
        vm.mins = false;
      }
      // 获取点击路由跳转时的书单主题
      vm.clifytitle = to.query.id;

      // 如果存在子分类(多页面)，则对各个子分类进行相应的数据请求
      if (vm.mins) {
        //   子分类有多少，则请求多少次
        for (var i = 0; i < vm.clifyMins.length; i++) {
          // i:子分类下标，vm.clifytitle：主分类名称，vm.classTag：分类主页的四大版块的键名
          vm.getnovelItem(i, vm.clifytitle, vm.classTag);
        }
      } else {
        //  否则，直接进行数据请求
        vm.getnovelItem(0, vm.clifytitle, vm.classTag);
        console.log(vm.clifytitle, vm.classTag);
      }

      //   console.log(vm.mins);
      console.log(vm.novelItem);
    });
  }
};
</script>

<style lang="less" scoped>
.titleBar {
  display: flex;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border: 1px solid #ebedf0;
  & > div {
    flex: 1;
  }
  .return {
    font-size: 14px;
    color: red;
  }
  .title {
    flex: 3;
    font-size: 18px;
  }
}
</style>