<template>
  <div class="home">
    <HomeLink></HomeLink>
    <!-- <div class="title">书城</div> -->
    <navTitle>智慧书屋</navTitle>
    <searchInput :hotcommend="searchCommend"  @s-focus="handel"></searchInput>
    <van-tabs>
      <van-tab v-for="(item,index) in TabTitle" :key="index" :title="item"></van-tab>
    </van-tabs>

    <van-swipe :autoplay="3000" class="lunbo">
      <van-swipe-item v-for="(image, index) in images" :key="index">
        <img v-lazy="image" />
      </van-swipe-item>
    </van-swipe>
    <div class="menu-list">
      <div @click="routerToPlayListPage">
        <i class="classify"></i>
        <h4>分类</h4>
      </div>
      <div>
        <i class="line"></i>
        <h4>排行</h4>
      </div>
      <!-- <div>
        <i class="vip"></i>
        <h4>VIP</h4>
      </div> -->
      <div @click="$router.push('/My')">
        <i class="hotText"></i>
        <h4>充值</h4>
      </div>
      <div>
        <i class="bookList"></i>
        <h4>书单</h4>
      </div>
    </div>
    <template v-if="showIf">
      <div class="getClassify" v-for="(item,index) in ItemLength" :key="index">
        <div class="classifyTitle">
          <div class="title" :id="maodian[index]">{{TabTitle[index]}}</div>
          <div class="more">查看更多 ></div>
        </div>
        <homepageClassify :novelItem="novelItem[index]" :booklist="booklist[index]"></homepageClassify>
      </div>
    </template>
  </div>
</template>

<script>
// https://novel.kele8.cn/rank/54d42d92321052167dfb75e3
// @ is an alias to /src
import HomeLink from "@/components/HomeLink.vue";
import searchInput from "@/components/searchInput.vue";
import navTitle from "@/components/navTitle.vue";
import homepageClassify from "@/components/homepageClassify.vue";

import l1 from "../assets/lunbo1.jpg";
import l2 from "../assets/lunbo2.jpg";
import l3 from "../assets/lunbo3.jpg";

import Vue from "vue";

import { Swipe, SwipeItem } from "vant";
Vue.use(Swipe);
Vue.use(SwipeItem);

import { Lazyload } from "vant";
Vue.use(Lazyload);

import { Tab, Tabs } from "vant";

Vue.use(Tab);
Vue.use(Tabs);

export default {
  name: "Home",
  components: {
    HomeLink,
    searchInput,
    navTitle,
    homepageClassify
  },
  data() {
    return {
      images: [l1, l2, l3],
      TabTitle: [
        "极品神作",
        "人气好书",
        "热门搜索",
        "口碑好评",
        "高能剧情",
        "小众佳作"
      ],
      maodian: ["m1", "m2", "m3", "m4", "m5", "m6"],
      rankData: null,
      novelItem: [],
      booklist: [],
      showIf: false,
      ItemLength: 0,
      searchCommend: null,
      
    };
  },
  methods: {
    getClassify(index) {
      const cacheBookListDetail = JSON.parse(
        window.localStorage.getItem("sl-" + this.rankData[index]._id)
      );
      if (cacheBookListDetail && cacheBookListDetail.expire > Date.now()) {
        this.novelItem[index] = cacheBookListDetail.rank.ranking.books[0];
        this.booklist[index] = cacheBookListDetail.rank.ranking.books.slice(
          1,
          5
        );
        this.ItemLength = this.novelItem.length;
        if (this.ItemLength == 6) {
          this.showIf = true;
        }
      } else {
        this.axios
          .get("/rank/" + this.rankData[index]._id)
          .then(response => {
            window.localStorage.setItem(
              "sl-" + this.rankData[index]._id,
              // 过期时间1小时
              JSON.stringify({
                expire: Date.now() + 3 * 60 * 60 * 1000,
                rank: response.data
              })
            );
            console.log(response);
            this.novelItem[index] = response.data.ranking.books[0];

            this.booklist[index] = response.data.ranking.books.slice(1, 5);

            this.ItemLength = this.novelItem.length;
            if (this.ItemLength == 6) {
              this.showIf = true;
            }

            console.log(this.novelItem.length);
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    routerToPlayListPage: function() {
      this.$router.push({ path: "classifyPage" });
    },
    handel($event){
      this.$emit('onfocus',true,$event)
      this.$router.push('/search')
      console.log($event)
    }
  },
  created() {
    const cacheBookListDetail = JSON.parse(
      window.localStorage.getItem("sl-rank-category")
    );
    if (cacheBookListDetail && cacheBookListDetail.expire > Date.now()) {
      this.rankData = cacheBookListDetail.rankCategory;
      for (var i = 0; i < this.TabTitle.length; i++) {
        this.showIf = false;
        this.getClassify(i);
      }
    } else {
      this.axios
        .get("/rank-category")
        .then(response => {
          window.localStorage.setItem(
            "sl-rank-category",
            // 过期时间1小时
            JSON.stringify({
              expire: Date.now() + 3 * 60 * 60 * 1000,
              rankCategory: response.data.male
            })
          );
          console.log(response);
          this.rankData = response.data.male;
          for (var i = 0; i < this.TabTitle.length; i++) {
            this.showIf = false;
            this.getClassify(i);
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    }

    // 请求热门推荐书籍，用在input滚动处
    this.axios
      .get("/hot-books")
      .then(response => {
        console.log(response);
        this.searchCommend = response.data.hotWords;
      })
      .catch(function(error) {
        console.log(error);
      });
  }
};
</script>
