<template>
  <div style="position: relative;overflow: hidden;">
    <div class="loading" v-if="showLoading">
      <div class="maskcicle"></div>
      <van-loading vertical type="spinner" size="36px">加载中...</van-loading>
    </div>
    <template v-if="renderIf">
      <div class="clickMaskBox">
        <div class="showIf" @click="showIf=!showIf"></div>
        <div class="clickMask">
          <div @click="clickLeft()"></div>
          <div @click="clickRight()"></div>
        </div>
      </div>
      <div class="setTopBox" :style="{top: showIf?0:-40+'px'}">
        <div class="setTopFle">
          <div class="bookname" @click="$router.go(-1)">
            <span>〈</span>
            <span>{{bookName}}</span>
          </div>
        </div>
      </div>
      <div class="setBottomBox" :style="{bottom: showIf?0:-40+'px'}">
        <div class="setBottomFle"></div>
      </div>

      <div class="contentBox">
        <div class="contentTitle">{{chaptersText.title}}</div>
        <div class="contentMove" :style="{transform: 'translateX('+moveXY+'px)'}">
          <div class="content">
            <p v-for="(item,index) in chaptersContent" :key="index">{{ item }}</p>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import Vue from "vue";
import { Loading } from "vant";
import { Toast } from "vant";

Vue.use(Toast);
Vue.use(Loading);

export default {
  data() {
    return {
      bookSource: null,
      chaptersLink: null,
      chaptersContent: [],
      chaptersText: null,
      renderIf: false,
      moveXY: 0,
      chapterLenght: 0,
      length: 0,
      showIf: false,
      bookName: null,
      showLoading: false,
      screenWidth:document.body.clientWidth
    };
  },
  methods: {
    getCharter() {
      this.axios
        .get("/book-chapters/" + this.bookSource)
        .then(response => {
          console.log(response);
          console.log("章节列表=>" + response);
          response.data.chapters[0].link;
          this.chaptersLink = encodeURIComponent(
            response.data.chapters[this.length].link
          );
          this.getCharterContent();
        })
        .catch(function(error) {
          error;
          console.log(1);
        });
    },
    getCharterContent() {
      // 每次创建组件都拿数据 太频繁 本地存储
      const cacheSongListDetail = JSON.parse(
        window.localStorage.getItem('sl-'+this.chaptersLink)
      );
      if (cacheSongListDetail && cacheSongListDetail.expire > Date.now()) {
        // 存在并且还没有过期
        this.renderIf = true;
        this.chaptersText = cacheSongListDetail.chaptersText;
        this.chaptersContent = this.chaptersContent.concat(
          cacheSongListDetail.chaptersText.cpContent.split(/[(\r\n)\r\n]+/)
        );
        this.showLoading = false;
      } else {
        this.axios
          .get("/chapters/" + this.chaptersLink)
          .then(response => {
            // 获取到的数据放入本地存储
            window.localStorage.setItem(
              "sl-"+this.chaptersLink,
              // 过期时间1小时
              JSON.stringify({
                expire: Date.now() + 3 * 60 * 60 * 1000,
                chaptersText: response.data.chapter
              })
            );

            console.log(response);
            this.renderIf = true;
            // this.chaptersContent=response.data.chapter.cpContent
            this.chaptersText = response.data.chapter;
            // this.chaptersContent = response.data.chapter.cpContent.split(
            //   /[(\r\n)\r\n]+/
            // );

            this.chaptersContent = this.chaptersContent.concat(
              response.data.chapter.cpContent.split(/[(\r\n)\r\n]+/)
            );
            this.showLoading = false;
            // console.log(arr)
          })
          .catch(function(error) {
            error;
            console.log(1);
          });
      }
    },
    // 点击屏幕左边，left值+（设备宽度-25）
    clickLeft() {
      if (this.moveXY >= 0) {
        // console.log("已经是第一页了");
        Toast("已经是第一页了");
        return;
      }
      this.moveXY = this.moveXY + (this.screenWidth-25);
      console.log(this.moveXY);
    },
    clickRight() {
      let content = this.$el.querySelector(".content");
      this.chapterLenght = content.getBoundingClientRect().width;
      console.log(this.chapterLenght);
      if (this.moveXY * -1 > this.chapterLenght - (this.screenWidth-25)) {
        console.log(this.moveXY * -1);
        console.log("加载下一章");
        this.length++;
        // this.moveXY=0
        this.getCharter();
        return;
      }
      this.moveXY = this.moveXY - (this.screenWidth-25);
      console.log(this.moveXY);
    }
    // clickCenter(){
    //   this.showIF=true
    // }
  },
  beforeRouteEnter(to, from, next) {
    console.log(to.query.bookId);

    // 每次创建组件都拿数据 太频繁 本地存储
    const cacheSongListDetail = JSON.parse(
      window.localStorage.getItem("sl-" + to.query.bookId)
    );
    console.log(cacheSongListDetail)
    if (cacheSongListDetail && cacheSongListDetail.expire > Date.now()) {
      // 存在并且还没有过期
      // this.songListDetail = cacheSongListDetail.result;
      // 守卫回调
      next(vm => {
        // 通过 `vm` 访问组件实例
        vm.showLoading = true;
        vm.bookName = to.query.bookName;
        vm.bookSource = cacheSongListDetail.bookSource[0]._id;
        vm.getCharter();
      });
    } else {
      window.axios
        .get("/book-sources?view=summary&book=" + to.query.bookId)
        .then(response => {
          console.log(to.query.bookId);
          console.log(response);

          // 获取到的数据放入本地存储
          window.localStorage.setItem(
            "sl-" + to.query.bookId,
            // 过期时间1小时
            JSON.stringify({
              expire: Date.now() + 3 * 60 * 60 * 1000,
              bookSource: response.data
            })
          );
          next(vm => {
            vm.showLoading = true;
            vm.bookName = to.query.bookName;
            vm.bookSource = response.data[0]._id;
            vm.getCharter();
          });
        })
        .catch(function(error) {
          error;
          console.log(1);
        });
    }
  },
  mounted() {
    console.log(this.screenWidth)
    // 当屏幕大小发生变化时触发
    window.onresize = () => {
      return (() => {
        // 获取设备屏幕宽度并赋值
        this.screenWidth = document.body.clientWidth
        console.log(this.screenWidth)
      })()
    }
  }
};
</script>

<style lang="less" scoped>
.loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  z-index: 999;
  justify-content: center;
  align-items: center;
  .maskcicle {
    width: 120px;
    height: 120px;
    border-radius: 20px;
    background: #4b4b4c;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }
}

.clickMaskBox {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 9;
  .showIf {
    width: 100px;
    height: 200px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }
  .clickMask {
    display: flex;
    height: 100%;
    & > div {
      flex: 1;
    }
  }
}

.setTopBox {
  position: absolute;
  top: -40px;
  left: 0;
  height: 40px;
  width: 100%;
  background: #aaa7a5;
  z-index: 10;
  transition-duration: 0.5s;
  .setTopFle {
    display: flex;
    & > div {
      flex: 1;
      line-height: 40px;
      font-size: 18px;
      font-family: 正楷;
      color: black;
    }
  }
}

.setBottomBox {
  position: absolute;
  bottom: -40px;
  left: 0;
  width: 100%;
  height: 40px;
  background: #aaa7a5;
  transition-duration: 0.5s;
  .setBottomFle {
    display: flex;
  }
}

.contentBox {
  background-color: #aaa7a5;
  color: rgb(0, 0, 0);
  font-family: 正楷;
  letter-spacing: 1px;
  height: 100vh;
  overflow: hidden;
  .contentTitle {
    position: sticky;
    top: 0;
    left: 0;
    background-color: #aaa7a5;
    padding: 10px;
  }
  .contentMove {
    padding: 5px 25px;
    width: auto;
    height: 580px;
    column-count: 1;
    column-gap: 25px;
    transition-duration: 1s;
    p {
      text-indent: 2em;
      line-height: 30px;
    }
  }
}
</style>